import React from 'react';




const GaussianEquation = (x,props) => {
    
    return 100*(1/(props.b * Math.sqrt(2 * Math.PI)) * Math.exp(-0.5*((x - props.a)/props.b)**2))
    //return (1/(props.b * Math.sqrt(2 * Math.PI))) 
  }
  
  
  

  const GaussianData = (props) => {
    
    let xObj = [];
    let xArray = [];
    for (var i=-50; i<51; i++) {
      xObj.push({x:i, y:GaussianEquation(i/5,props)})
      xArray.push([i, GaussianEquation(i/5,props)])
    }
      
      return [xObj, xArray];
  }

  export default GaussianData;