import React from 'react';




const QuadraticEquation = (x,props) => {
  if (props.a !== undefined && props.a !== null) {
    
    return ( props.a*x**2 + props.b*x + props.c)
  } else if (props.a2 !== undefined && props.a2 !== null) {
    return ( props.a2*x**2 + props.b2*x + props.c2)
  }
  }
  
  const QuadraticData = (props) => {
    console.log("props in QuadraticData", props)
    let xObj = [];
    let xArray = [];
    for (var i=-50; i<51; i++) {
      xObj.push({x:i, y:QuadraticEquation(i,props)})
      xArray.push([i, QuadraticEquation(i,props)])
    }
      
      return [xObj, xArray];
  }

  export default QuadraticData;