import React, { Component } from 'react';
import cubicSVG from '../svgElements/cubicSVG.svg';
import GraphCreator from '../../graphCreators/graphCreator';
import "./styleResponsive.css";
import "./grid.css"

import {Tex} from 'react-tex';
import {InlineTex} from 'react-tex';
import {LaTeX} from 'react-latex';

const svgPicCubic = cubicSVG;

let aValue = 0, bValue = 0, cValue = 0, dValue = 0;


class CubicFunctions extends Component {
  

  increaseCoeff_a = () => { aValue += 0.5}
  decreaseCoeff_a = () => { aValue += -0.5}
  increaseCoeff_b = () => { bValue += 0.5}
  decreaseCoeff_b = () => { bValue += -0.5}
  increaseCoeff_c = () => { cValue += 0.5}
  decreaseCoeff_c = () => { cValue += -0.5}
  increaseCoeff_d = () => { dValue += 0.5}
  decreaseCoeff_d = () => { dValue += -0.5}

  

    render() {
        return (
            
              <div className="containerFunctions">
    <h3>Cubic functions. </h3>
      
      <div className="row">
        <div className="col-md-8" id="test">Cubic functions always include a cubic term i.e. <InlineTex texContent = {"$$x^3$$"} /> but cannot include any terms in powers higher than 3.</div>
        <div className="col-md-4"> <img src={svgPicCubic}/></div>
      </div>
      <div className="row">
        <div className="col-md-8"><GraphCreator graphType="cubic" a={aValue} b={bValue} c={cValue} d={dValue}/></div>
        <div className="col-md-4" align="center">
        <div className="btn-group-vertical">
        <a  href="#section-z" className="btn btn-primary" onClick={this.increaseCoeff_a} >Increase coeffcient a</a> 
        <a  href="#section-z" className="btn btn-primary" onClick={this.decreaseCoeff_a}>Decrease coeffcient a</a>
        <a  href="#section-z" className="btn btn-primary" onClick={this.increaseCoeff_b}>Increase coeffcient b</a> 
        <a  href="#section-z" className="btn btn-primary" onClick={this.decreaseCoeff_b}>Decrease coeffcient b</a>
        <a  href="#section-z" className="btn btn-primary" onClick={this.increaseCoeff_c}>Increase coeffcient c</a> 
        <a  href="#section-z" className="btn btn-primary" onClick={this.decreaseCoeff_c} margin="5px">Decrease coeffcient c</a>
        <a  href="#section-z" className="btn btn-primary" onClick={this.increaseCoeff_d}>Increase coeffcient d</a> 
        <a  href="#section-z" className="btn btn-primary" onClick={this.decreaseCoeff_d} margin="5px">Decrease coeffcient d</a> 
        </div>
        <br/><br/>
        <table align="center">
          <col width="40px" /><col width="70px"/><col width="70px" />
          <col width="70px" /><tr>
          <th style={{color:"blue" }}></th>
              <th style={{color:"#29b6f6" }}>a = {aValue}</th>
              <th style={{color:"#29b6f6" }}>b = {bValue}</th>
              <th style={{color:"#29b6f6" }}>c = {cValue}</th>
              <th style={{color:"#29b6f6" }}>d = {dValue}</th>
            </tr>
          </table>
        
      </div>
      
      </div>
    </div>
        )
    }
}

export default CubicFunctions;