import React from 'react';
//import { extent as d3ArrayExtent } from 'd3-array';
import {
  axisBottom as d3AxisBottom,
  axisLeft as d3AxisLeft,
} from 'd3-axis';
import {
  scaleLinear as d3ScaleLinear
} from 'd3-scale';
import { select as d3Select } from 'd3-selection';
import { line as d3Line } from 'd3-shape';
import { mean as d3Mean } from 'd3-array';
import { sum as d3Sum } from 'd3-array';
import { format as d3Format } from 'd3-format';
import './SimPaths.css'
import SVGWithMargin from '../SVGWithMargin';





const width = 450;
const height = 150;
const margin = 40;

var pathCollector = [];
var terminalValueCollector = [];
var sampleAverage = 0.00;
var discSampleAverage = 0.00;
var nPathsSoFar = 0;

class SimPaths extends React.Component {


  selectX = d => d.time;
  selectY = d => d.r;
  
  
  
  xScale = d3ScaleLinear()
    .domain([0,20])
    .range([0, width]);

  yScale = d3ScaleLinear()
    .domain([0,10])
    .range([height, 0]);

  xAxis = d3AxisBottom()
    .scale(this.xScale)
    .ticks(this.props.nPeriods);
    
  yAxis = d3AxisLeft()
    .scale(this.yScale)
    .ticks(this.props.nPeriods);
    
  selectScaledX = datum => this.xScale(this.selectX(datum));
  selectScaledY = datum => this.yScale(this.selectY(datum));  
    
  graphLine = d3Line()
    .x(this.selectScaledX)
    .y(this.selectScaledY);

  linePath = this.graphLine(this.props.randomData); 

  pathFunction = datum => this.graphLine(datum);

  path1 = this.pathFunction(this.props.randomData);
  
  circlesFunction = datumMain => datumMain.map(datum => ({
    x: this.selectScaledX(datum),
    y: this.selectScaledY(datum),
  }));
  
    

  circlePoints = this.props.randomData.map(datum => ({
    x: this.selectScaledX(datum),
    y: this.selectScaledY(datum),
  }));

  
  add = (a, b) => {
    return a + b
  };

  
 
  // End of cumulative Normal methds.  ----------------------------

  render() {
 
    

    
    const  path1 = this.pathFunction(this.props.randomData);
    

    
    if (this.props.completedPath === 1) { 
    pathCollector[this.props.nPaths-1] = path1;
    }

    if (this.props.completedPath === 1) { 
      var lastObs = this.props.counter-2;
      terminalValueCollector.push(this.props.randomData[lastObs]["r"]);
      nPathsSoFar += 1;
      sampleAverage = (terminalValueCollector.reduce(this.add, 0) / nPathsSoFar).toPrecision(3);
      }

    if (this.props.completedBulkPaths === 1) { 
      terminalValueCollector.push(...this.props.bulkTerminalValues);
      nPathsSoFar += 100;
      sampleAverage = (terminalValueCollector.reduce(this.add, 0) / nPathsSoFar).toPrecision(3);
      discSampleAverage = ((1/(1+this.props.r_s)**10)*sampleAverage).toPrecision(3);
    }
    
    const strike = 3;
    const terminalValueCollectorCallStrike3 = new Array(terminalValueCollector.length);
    for (var i=0; i<terminalValueCollector.length; i++) {
      if (terminalValueCollector[i] <= strike) {terminalValueCollectorCallStrike3[i]=0} else {terminalValueCollectorCallStrike3[i] = terminalValueCollector[i] - strike}
    }

    const rnExpectation = d3Mean(terminalValueCollectorCallStrike3);
    const priceCallStrike3 = (1/(1+this.props.r_s)**10)*rnExpectation;
    const rnExpectationForDOM = d3Format(".2f")(rnExpectation);
    const priceCallStrike3ForDOM = d3Format(".2f")(priceCallStrike3);
  
    


  return( <div>
              
              <SVGWithMargin
                className="container2"
                contentContainerBackgroundRectClassName="contentContainerBackgroundRect"
                contentContainerGroupClassName="contentContainer"
                height={height}
                margin={margin}
                width={width}
                backgroundColor={"red"}>
              
              <g        // Draw the x axis.
                className="xAxis"
                ref={node => d3Select(node).call(this.xAxis)}
                style={{
                  transform: `translateY(${height}px)`,
                }}
              />
              <g       // Draw the y axis.
                className="yAxis" ref={node => d3Select(node).call(this.yAxis)}
                 /> 

              <g className="line" fill="transparent" stroke="#29b6f6" strokeWidth="3"               
              >               
                <path d={path1} />
                
                <path d={pathCollector[0]}/> 
                {pathCollector.map (i => (
                  <path d={pathCollector} />
                ))}
                </g>
              
                
                
                
              <g className="scatter">
              
                  {this.circlesFunction(this.props.randomData).map(circlePoint => (
                    <circle
                      cx={circlePoint.x}
                      cy={circlePoint.y}
                      key={`${circlePoint.x},${circlePoint.y}`}
                      r={2}
                    />
                  ))}
              </g>
              
              </SVGWithMargin> 
          </div>
  )
}
graphline = 0;
}

export default SimPaths;
