import React from 'react';
import HistogramWilliamson from "./histogramWilliamson";

 


//var histMin = 0;
//var histMax = 10;
var histNumberBuckets = 21;
var terminalValueCollectorHistogram = [];


class Histogram extends React.Component {

    
    histMin = this.props.histogramMin; 
    histMax = this.props.histogramMax;
    
    makeBuckets = () => {
        const buckets = [histNumberBuckets];
        for (var i=0; i<histNumberBuckets; i++){
            buckets[i] = this.histMin + i*(this.histMax-this.histMin)/histNumberBuckets
        }
        
        return buckets;   
    }
    
    
    buckets = this.makeBuckets();
    fillBuckets = (dataForBuckets) => {
        
        const countObs = new Uint32Array(histNumberBuckets); // Initiated for each button press.
        
        
        for (var i=0;i<histNumberBuckets; i++){  
            var countObsBefore = countObs[i];     
            for (var j=0;j<dataForBuckets.length; j++){         
                if (dataForBuckets[j] >= this.buckets[i] && dataForBuckets[j] < this.buckets[i+1]) { countObs[i] = countObs[i] + 1  };
            }
        } 
        
        return countObs;        
    }    
    
    
    render() {
        
        
        if (this.props.completedBulkPaths === 1 ) { 
            terminalValueCollectorHistogram.push(...this.props.bulkTerminalValues);
          }

          if (this.props.completedPath === 1) { 
            var lastObs = this.props.counter-2;
            terminalValueCollectorHistogram.push(this.props.randomData[lastObs]["r"]);
          }  

          
        const dataHistogram = this.fillBuckets(terminalValueCollectorHistogram);

        
        
        // <h2>Relative frequency of terminal values. </h2>
        return (
            <div>
                
                
                <HistogramWilliamson  width={500} height={150} dataHistogram={dataHistogram}
                 buckets={this.buckets} createdData={this.props.createdData}/>
            </div>
        )
    }   

}

export default Histogram;
