import React from "react";
import { range as d3Range } from "d3-array";
import { max as d3Max } from "d3-array";


import { scaleLinear as d3ScaleLinear } from 'd3-scale';
import { scaleOrdinal as d3ScaleOrdinal } from 'd3-scale';
import { format as d3Format } from 'd3-format';

class Bar extends React.Component {

  
  render() {
    let barHeight = 0
    
    
    let style = {
      fill: "steelblue"
    }
    
    if (this.props.createdData == 0){
      barHeight = 0
    } else {
      barHeight = this.props.barHeight
    }
     
      return(
        <g>
            <rect className="bar" style={style} x={this.props.x-13} y={this.props.y + 5} width={this.props.width} height={barHeight} />
        </g>
      )
  }
}
  
  class YAxis extends React.Component {
    
    render() {
      let style = {
        stroke: "steelblue",
        strokeWidth: "1px"
      }
      
      let textStyle = {
        fontSize: "0.8em",
        fill: "steelblue",
        textAnchor: "end"
      }
      
      //D3 mathy bits
      let ticks = d3Range(0, this.props.end, (this.props.end / this.props.labels.length))
      let percentage = d3Format(".0%")
      
      let lines = []
      ticks.forEach((tick, index) => {
        lines.push(<line style={style} y1={tick} x1={this.props.y} y2={tick} x2={this.props.y - 4}  />)
      })
      
      let columnLables = []
      ticks.forEach((tick, index) => {
        columnLables.push(<text style={ textStyle } y={tick + 6} x={this.props.y - 6} fontFamily="Verdana" >{percentage(this.props.labels[index])}</text>)
      })
      
    
      return(
        <g>
            <g className="y_labels" transform={`translate(${-5},${17})`}>
            <line x1={this.props.y} y1={this.props.start} y2={this.props.end} x2={this.props.y} style={ style } />
            </g>
      
            <g className="y_labels" transform={`translate(${-5},${51})`}>
              { columnLables }
              { lines }
            </g>
        </g>
      )
    }
  
  }
  
  class XAxis extends React.Component {
   
    render() {
       
      let style = {
        stroke: "steelblue",
        strokeWidth: "1px"
      }
      
      let step = (this.props.start + this.props.end / this.props.labels.length)
      
      //D3 mathy bits   
      let ticks = d3Range(this.props.start, this.props.end, step)
      
      let lines = []
      ticks.forEach((tick, index) => {
        lines.push(<line style={style} x1={tick + 10 } y1={this.props.x} x2={tick + 10} y2={this.props.x + 4}  />)
      })
      
      let columnLabels = []
      ticks.forEach((tick, index) => {
      columnLabels.push(<text style={{fill: "steelblue"}} x={tick+5} y={this.props.x + 20} fontFamily="Verdana" fontSize="12">{d3Format(".1f")(this.props.labels[index])}</text>)
      })
      
      
      return(
        <g>
            <line x1={this.props.start} y1={this.props.x } x2={this.props.end} y2={this.props.x} style={ style } />
            { columnLabels }
            { lines }
        </g>
      )
    }
  
  }
  
  class HistogramWilliamson extends React.Component {
  
    render() {

      let dataX = this.props.buckets
      let dataY = this.props.dataHistogram
  
      let margin = {top: 20, right: 20, bottom: 30, left: 35},
        width = this.props.width - margin.left - margin.right,
        height = this.props.height - margin.top - margin.bottom;
  
      
      //D3 mathy bits    
      let ticks = d3Range(0, width, (width / dataX.length))
     
      let x = d3ScaleLinear()
        .domain(dataX)
        .range(ticks)
        
        let y = d3ScaleLinear()
        //.domain([0, d3Max(data, (d) => d.frequency)])
        .domain([0, d3Max(dataY)])    
        .range([height, 0])
        
  


      let bars = []
      let bottom = 200
      
      
      let maxdataY = d3Max(dataY)
      
      for (var i=0; i< dataX.length-1; i++){
        
        if (maxdataY === 0) {
          bars.push(<Bar  x={x(dataX[i+1])} y={bottom - 6 - (height - y(dataY[i]))} width={20} barHeight={height - 0} createdData={0}/>)
        } else {
          bars.push(<Bar  x={x(dataX[i+1])} y={bottom - 6 - (height - y(dataY[i]))} width={20} barHeight={height - y(dataY[i])} createdData={1}/>)
        }

        

          
        
      }
      

      //dataX.forEach((datum, index) => {
      //  bars.push(<Bar key={index} x={x(datum)} y={bottom - 6 - (height - y(dataY))}//width={20} height={height - y(dataY)} />)

      //data.forEach((datum, index) => {
      //  bars.push(<Bar key={index} x={x(datum.letter)} y={bottom - 6 -(height - y(datum.frequency))} width={20} height={height - (datum.frequency)} />)
      
      //<YAxis y={40} labels={y.ticks().reverse()} start={15} end={height} /> 
  
      return (
        <div>
        
        <svg width={this.props.width} height={this.props.height+160}>
            
            
            <g className="chart" transform={`translate(${margin.left},${margin.top})`}>
               { bars }
               <XAxis x={ bottom } labels={dataX} start={0} end={width} />
            </g>
        </svg>
        
        </div>
      );
    }
  
  }
  
  export default HistogramWilliamson;
  