import React, { Component } from 'react';

import './App.css';


import SimPaths from "./graphicsEngine/plots/SimPaths";


import Histogram from "./graphicsEngine/plotCode/histogram";
import { randomNormal as d3Normal } from "d3-random";
import './bootstrap.min.css';
import BlackScholes from "./BlackScholes"

var nPaths = [];
var completedPath = 0;

const r_s = 0.01;  // Annual rate of 12%, using monthly intervals.   
var sigma = 0.08;  // 9.6% annually

const nPeriods = 21    // Includes the starting period t = 0.
const nBulkPaths = 100 // Number of terminal values when we create these in bulk. //

class PathButtons extends React.Component {
  
  render(){    
    console.log("PathButtons props = ", this.props)
      return ( 
        
      <div id="monteCarlo" style={{marginTop:"5px", marginLeft:"60px"}} >
        <div>
                <button  style={{height:" 30px", width:"230px"}} onClick={this.props.playButton} >
                    Create a rw path
                </button>   
                <button  style={{height:" 30px", width:"230px"}} onClick={this.props.bulkButton} >
                    Add 100 rw terminal values
                </button>   
        </div>         
       <div>
                  <button  style={{height:" 30px", width:"230px"}} onClick={this.props.playButtonABM}>
                      Create an ABM path
                  </button> 
                  <button  style={{height:" 30px", width:"230px"}} onClick={this.props.bulkButtonABM}>
                      Add 100 ABM terminal values
                  </button>         
        </div>
       <div>
       <button  style={{height:" 30px", width:"230px"}} onClick={this.props.playButtonGBM}>
       Create a GBM path
   </button> 
   <button  style={{height:" 30px", width:"230px"}} onClick={this.props.bulkButtonGBM}>
       Add 100 GBM terminal values
   </button>
   </div>
   {
   //<button  style={{height:" 30px", width:"460px"}} onClick={this.props.clearAllButton}>
  //Unused button - ignore me!
    // </button>
   }  
  <div>

  </div>
   
      
      </div>
    
      )
    }
  }
      
       



class SimulationPaths_simplified extends Component {
  constructor(){
    super();
    this.speed= 50;
    this.state = {
      counter: 0,
      randomGraphData: [],
      nPaths: nPaths,
      completedPath: 0,
      bulkTerminalValues: [],
      completedBulkPaths: 0,
      nBulkPaths: nBulkPaths,
      clearAll: 0
    };
  }


/*
** counter: records the total number of paths that have been created (does not include those created only for their terminal values).
** randomGraphData: the data for the most recent path. These are stored in <Axes />, only the most recent one appears here.
** nPaths: counter for the number of paths created - updated on each path-creation button press.
** completedPath: Switches from 0 to 1 when a path has been completed - sent to <Axes /> and used to count the number of paths. Used in calculating sample averages.
** bulkTerminalValues: The terminal values from a single press of a bulk button.
** completedBulkPaths: Switches from 0 to 1 when the lastest bulk path creation exercise is complete. Passed to <Axes/> where it triggers some updates.
** nBulkPaths: pass the user-supplied number of paths in the bulk paths creation to the <Axes /> component.
/*



  /*
  ** Create rw, ABM and GBM paths.
  */

  randomise = (length) => {
    let rr = new Array(length);
    rr[0] = 5;
    for(var i=1;i<length;i++){
      rr[i] = rr[i-1] + Math.random() - 0.5;
    }
    return rr;
  }

  randomiseABM = (length,r_s,sigma) => {
    let rr = new Array(length);
    rr[0] = 5;
    for(var i=1;i<length;i++){
      rr[i] = rr[i-1] + r_s + sigma*d3Normal()();
    }
    return rr;
  }

  randomiseGBM = (length,r_s,sigma) => {
    let rr = new Array(length);
    rr[0] = 5;
    for(var i=1;i<length;i++){
      rr[i] = rr[i-1] + (r_s*rr[i-1]) + sigma*rr[i-1]*d3Normal()();
    }
    return rr;
  }


/*
** Create bulk terminal values for rw, ABM and GBM paths.
*/


  randomiseBulk = (length) => {
    let rr = new Array(length);
    rr[0] = 5;
    for(var i=1;i<length;i++){
      rr[i] = rr[i-1] + Math.random() - 0.5;
    }
    return rr[length-1];
  }

  randomiseBulkABM = (length,r_s,sigma) => {
    let rr = new Array(length);
    rr[0] = 5;
    for(var i=1;i<length;i++){
      rr[i] = rr[i-1] + r_s + sigma* d3Normal()();
      
    }
    
    return rr[length-1];
  }

  randomiseBulkGBM = (length,r_s,sigma) => {
    let rr = new Array(length);
    rr[0] = 5;
    for(var i=1;i<length;i++){
      rr[i] = rr[i-1] + (r_s*rr[i-1]) + sigma*rr[i-1]* d3Normal()();
    }
    return rr[length-1];
  }

  
  /*
  ** Method to store the data in js objects.
  */

  storeData = (pathData) => {
    let xx = {time: 0, r: 0}
    let dd = new Array(nPeriods);
        for (var i=0; i<nPeriods;i++){
          xx = {time: i, r: pathData[i]}
          dd[i] = xx
        }
        return dd;
    }

    replaceData = (d,add) => {
      
      let xx = Object.create(d[0]);
      let dd = new Array(d.length);
      for(var i=0;i<d.length;i++){
        xx = Object.create(d[0]);
        xx.time = d[i].time; 
        xx.r = add[i];
        dd[i] = xx;
      }
      
      
      return dd;
    }
  
  /*
  ** Store the data for complete paths (as opposed to just the terminal values).
  */ 


  randomData =this.storeData(this.randomise(nPeriods))
  randomDataABM =this.storeData(this.randomiseABM(nPeriods,r_s,sigma))
  randomDataGMB =this.storeData(this.randomiseGBM(nPeriods,r_s, sigma))

  /*
  ** Button event handlers for creating single paths.
  */
 
  playButton = () => {
    clearInterval(this.intervalId);
    this.intervalId = setInterval(this.play,this.speed);
    this.randomData =this.storeData(this.randomise(nPeriods))
    
    this.setState({
      counter: 0,
      completedPath: 0,
      completedBulkPaths: 0
    });
  }
 
  playButtonABM = () => {
    clearInterval(this.intervalId);
    this.intervalId = setInterval(this.play,this.speed);
    this.randomData =this.storeData(this.randomiseABM(nPeriods,r_s,sigma));
    
    this.setState({
      counter: 0,
      completedPath: 0,
      completedBulkPaths: 0
    });
  }
 
  playButtonGBM = () => {
    clearInterval(this.intervalId);
    this.intervalId = setInterval(this.play,this.speed);
    this.randomData =this.storeData(this.randomiseGBM(nPeriods,r_s,sigma));
    this.setState({
      counter: 0,
      completedPath: 0,
      completedBulkPaths: 0
    })
  }


  
  /*
  ** Button event handlers for creating bulk terminal values.
  */ 


  bulkButton = () => {
    
    var bulkTerminalValues=[nBulkPaths];
    for (var i=0; i<nBulkPaths; i++){
    bulkTerminalValues[i] = this.randomiseBulk(nPeriods)}
    this.setState({
      bulkTerminalValues: bulkTerminalValues,
      completedBulkPaths: 1,
      completedPath: 0,
    });
  }

  bulkButtonABM = () => {
    
    var bulkTerminalValues=[nBulkPaths];
    for (var i=0; i<nBulkPaths; i++){
    bulkTerminalValues[i] = this.randomiseBulkABM(nPeriods,r_s,sigma)}
    this.setState({
      bulkTerminalValues: bulkTerminalValues,
      completedBulkPaths: 1,
      completedPath: 0
    });
  }

  bulkButtonGBM = () => {
    var bulkTerminalValues=[nBulkPaths];
    for (var i=0; i<nBulkPaths; i++){
      bulkTerminalValues[i] = this.randomiseBulkGBM(nPeriods,r_s,sigma);}
      this.setState({
        bulkTerminalValues: bulkTerminalValues,
        completedBulkPaths: 1,
        completedPath: 0,
      });
  }


  clearAllButton = () => {
    console.log("clearAllButton")
    
    this.setState({
      clearAll: 1
    });
    console.log("this.state = ", this.state)
  }

  pauseButton = () => {
    clearInterval(this.intervalId);
}

  stopSim = () => {
    clearInterval(this.intervalId);
  }


/*
** Create data to send to graphics components (stored in state).
*/



  play = () => {
    let g = this.state.counter;
    let g2 = g + 1;
    
    if (g2 > nPeriods){this.stopSim(); nPaths++; completedPath = 1;} else{completedPath=0}
      this.setState({
        counter: g2,
        randomGraphData: this.randomData.slice(0,g2 + 2),
        nPaths: nPaths,
        completedPath: completedPath
      });
    }

  playABM = () => {
    let g = this.state.counter;
    let g2 = g + 1;
    
    if (g2 > nPeriods){this.stopSim(); nPaths++; completedPath = 1;} else{completedPath=0}

      this.setState({
        counter: g2,
        randomGraphData: this.randomDataABM.slice(0,g2 + 2),
        nPaths: nPaths,
        completedPath: completedPath
      });
    }
  
  playGBM = () => {
    let g = this.state.counter;
    let g2 = g + 1;
    
    if (g2 > nPeriods){this.stopSim(); nPaths++; completedPath = 1;} else{completedPath=0}

      this.setState({
        counter: g2,
        randomGraphData: this.randomDataGBM.slice(0,g2 + 2),
        nPaths: nPaths,
        completedPath: completedPath
      });
    }

    
  render() {
    

    return (
      <div className="App-div" style={{border:"1px solid #000"}}>
      <div className="row">
      <div className="col-md-6">
        <PathButtons
          playButton={this.playButton}          
          bulkButton={this.bulkButton}

          playButtonABM={this.playButtonABM}          
          bulkButtonABM={this.bulkButtonABM}

          playButtonGBM={this.playButtonGBM}          
          bulkButtonGBM={this.bulkButtonGBM}
          clearAllButton={this.clearAllButton}
        />
      </div>
      
      <div className="col-md-6">
      <SimPaths  randomData={this.state.randomGraphData} nPaths={this.state.nPaths} completedPath={this.state.completedPath} counter={this.state.counter} bulkTerminalValues={this.state.bulkTerminalValues} completedBulkPaths={this.state.completedBulkPaths} r_s={r_s} nBulkPaths = {this.state.nBulkPaths}
        clearAll = {this.state.clearAll}/>
        </div>
      
    </div>
    <div className="row" style={{height: "265px"}}>
    
    <div className="col-md-6"  >
    <BlackScholes  randomData={this.state.randomGraphData} nPaths={this.state.nPaths} completedPath={this.state.completedPath} counter={this.state.counter} bulkTerminalValues={this.state.bulkTerminalValues} completedBulkPaths={this.state.completedBulkPaths} r_s={r_s} nBulkPaths = {this.state.nBulkPaths}
        />
    </div>

    <div className="col-md-6" >
        <Histogram completedBulkPaths={this.state.completedBulkPaths} bulkTerminalValues={this.state.bulkTerminalValues} completedPath={this.state.completedPath} counter={this.state.counter} randomData={this.state.randomGraphData} histogramMin={0} histogramMax={21} createdData={this.state.createdData}/>
    </div>
   </div></div>
    );
  }
}

export default SimulationPaths_simplified;
