import React from 'react';




const CubicEquation = (x,props) => {
  
    return ( props.a*x*x*x + props.b*x*x + props.c*x + props.d)
  }
  
  const CubicData = (props) => {
    console.log("props in QData", props)
    let xObj = [];
    let xArray = [];
    for (var i=-50; i<51; i++) {
      xObj.push({x:i, y:CubicEquation(i/5,props)})
      xArray.push([i, CubicEquation(i/5,props)])
    }
      
      return [xObj, xArray];
  }

  export default CubicData;