import React from "react"
import * as brain from "brain.js"



const Training = (matchResults) => {
  const r = matchResults;
  const network = new brain.NeuralNetwork();
  const res = [0,0,0,0,0,0,0,0]

  console.log("matchResults = ", matchResults)

  var home = '';
  var away = '';
  
  if (r.home === 'A'|| r.home === 'a'){
    home = 1
  } else if (r.home === 'B'|| r.home === 'b'){
    home = 2
  }else if (r.home === 'C'|| r.home === 'c'){
    home = 3
  }else if (r.home === 'D'|| r.home === 'd'){
    home = 4
  }

  if (r.away === 'A' || r.away === 'a'){
    away = 1
  } else if (r.away === 'B' || r.away === 'b'){
    away = 2
  }else if (r.away === 'C' || r.away === 'c'){
    away = 3
  }else if (r.away === 'D' || r.away === 'd'){
    away = 4
  }

  
  console.log("home = ", home)
  console.log("away = ", away)

  if (r.a1 < r.b1){
    res[0] = 1 
  }
  if (r.a2 < r.c2){
    res[1] = 1 
  }
  if (r.a3 < r.d3){
    res[2] = 1 
  }
  if (r.b4 < r.c4){
    res[3] = 1 
  }
  if (r.b5 < r.d5){
    res[4] = 1 
  }
  if (r.c6 < r.d6){
    res[5] = 1 
  }
  if (r.c7 < r.a7){
    res[6] = 1 
  }
  if (r.d8 < r.c8){
    res[7] = 1 
  }

  console.log("res vector = ", res)





  network.train([
    { input: [1, 2], output: [res[0]] }, // Team 1 wins
    { input: [1, 3], output: [res[1]] }, 
    { input: [1, 4], output: [res[2]] }, 
    { input: [2, 3], output: [res[3]] } 
  ],
  {log: false});
  
  const output = network.run([home, away]);
  const prob_1 = 1 - output
  
  console.log(`Prob that team ${home} at home against team ${away} is ${prob_1}`);
  return prob_1
}





export default Training
