import React from 'react';




const ExponentialEquation = (x,props) => {
  
    return ( props.a * Math.exp(props.b * x))
  }
  
  const ExponentialData = (props) => {
    
    let xObj = [];
    let xArray = [];
    for (var i=-50; i<51; i++) {
      xObj.push({x:i, y:ExponentialEquation(i/5,props)})
      xArray.push([i, ExponentialEquation(i/5,props)])
    }
      
      return [xObj, xArray];
  }

  export default ExponentialData;